@import './questions-nav';
@import './questions-panel';


.questionnaires-page{
    background-color: #fff;
    height: 100%;
    width: 100%;

    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: flex-start;


    &__buttons {
        margin-top: auto;
        padding: 2rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

        .button{
            width: calc(50% - .5rem);
        }
    }
}