.disclaimer{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #fff;
    padding: 2rem;
    border-top-left-radius: 1.5rem;
    border-top-right-radius: 1.5rem;
    transform: translateY(100%);
    transition: all .25s ease-in-out;

    &--show {
        transform: translateY(0%);
    }


    &__title {
        color: $theme;
        font-size: 2rem;
        margin: 1.5rem 0;
    }

    &__text {
        color: $text-color-light;
        font-size: 1.5rem;

        &--accept{
            color: rgba($theme, .6);
            margin: 5rem 0 4rem;
            position: relative;
            padding-left: 2.5rem;

            strong{
                color: $theme;
                font-weight: normal;
            }

            &::before{
                content: '';
                width: 1.8rem;
                height: 1.8rem;
                border: 1px solid $theme;
                position: absolute;
                left: 0;
                border-radius: 2px;
            }        
            
            &::after{
                transition: all .25s ease-in-out;
                background-color: #fff;
                content: '';
                width: 1.2rem;
                height: 1.2rem;
                position: absolute;
                top: 2.5px;
                left: 2.5px;
            }
        }

        &--checked {
            &::after{
                background-color: $theme;
            }
        }
    }

    .button{
        width: 100%;
    }

}