.questions-nav{

    display: flex;
    align-items: center;
    justify-content: center;
    height: 8rem;



    &__dot {
        width: 1rem;
        height: 1rem;
        border-radius: 50%;
        background-color: rgba($theme, .5);
        cursor: pointer;
        transition: all .25s ease-in-out;

        &:not(:last-child){
            margin-right: 1rem;
        }

        &--done {
            background-color: $theme;
        }
    }

}